<template>
    <div class="loading" style="background:#eee;">
        <div class="loader13 loader">
            <div class="loader-13">
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
            </div>
        </div>
    </div>
</template>

<script>
import '../css/pace-theme-big-counter.css'
import '../js/pace.min.js'
export default {
    name: 'loading',
    data() {
        return {
            msg: 'loading'
        }
    },
    created() {
        this.loading()
    },
    methods: {
        loading() {
            let that = this
            $('.swiper-box').css('visibility', 'hidden')
            Pace.on('done', function() {
                Pace.stop()
                // $('.swiper-box').css('visibility', 'visible')
                console.log('加载完成')
                that.$router.push('index')
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loading {
    width: 100%;
    height: 100%;
    position: relative;
}

.loader {
    width: 60%;
    height: 10rem;
    position: absolute;
    top: 30%;
    left: 20%;
}
</style>



